import {Component} from '@angular/core';
import {SecurityService} from "./service/security.service";

/**
 * @author Alex Brui
 */
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  static readonly MIN_USERNAME_LENGTH = 3;
  static readonly MAX_USERNAME_LENGTH = 25;
  static readonly MIN_FIRST_NAME_LENGTH = 1;
  static readonly MAX_FIRST_NAME_LENGTH = 25;
  static readonly MIN_PASSWORD_LENGTH = 6;
  static readonly MAX_PASSWORD_LENGTH = 25;
  static readonly MIN_FILE_TYPE_NAME = 3;
  static readonly MAX_FILE_TYPE_NAME = 25;
  static readonly MIN_FILE_TYPE_MIME = 3;
  static readonly MAX_FILE_TYPE_MIME = 25;
  static readonly MIN_FILE_TYPE_EXTENSION = 2;
  static readonly MAX_FILE_TYPE_EXTENSION = 5;
  /**
   * The flag that the user is authed.
   */
  private _authed: boolean;

  constructor(private readonly security: SecurityService) {
    this._authed = false;
    this.security.authProperty
      .subscribe(result => this._authed = result);
  }

  logout() {
    this.security.logout();
  }

  /**
   * The flag that the user is authed.
   *
   * @returns {boolean}
   */
  get authed(): boolean {
    return this._authed;
  }

  /**
   * Check the role in the current user.
   *
   * @param {string} toCheck the role to check.
   * @returns {boolean} true if the current user has the role.
   */
  hasRole(toCheck: string): boolean {
    return this.security.hasRole(toCheck);
  }
}



